.nav-bar {
  position: sticky;
  top: 0;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  height: 100vh;
  min-height: 100%;
  width: 350px;
  min-width: 350px;
  box-shadow: 5px 0 30px 0px rgba(0, 0, 0, 0.178);
  background-image: linear-gradient(to bottom right, #568548, #89cc62);
  /* border-bottom-right-radius: 35px;
  border-top-right-radius: 35px; */

  transition: all 1s ease-out;
}

.nav-links {
  font-family: "Segoe UI";
  color: #545554;
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  background-color: white;
  padding: 0 0;
  margin: -80px 0 0 0;
  /* height: 450px; */

  transform: translateX(0);
}

.nav-link {
  color: #545554;
  text-decoration: none;
  /* font-size: 3vh; */
  font-size: 1.4rem;
  padding: 4% 25px;
  border-bottom: 1px solid #54555471;
  transition: transform 0.2s ease-out;
  height: 16.67%;
}

.nav-link:hover {
  color: white;
  background-color: #b7da98;
  border-bottom: 1px solid #ffffff71;
  transform: translateX(10px);
  transition: transform 0.2s ease-out;
}

/* .last-link {
  border-bottom: none;
} */

.lower-nav {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin: 0;
}

.lower-nav img {
  align-self: flex-end;
  width: 25vh;
  margin-right: 15px;
}

.avatar {
  display: flex;
  align-items: center;
  position: relative;
  /* margin: -20px 0 0 50px; */
  top: -80px;
  width: 40%;
  left: 50px;
}

.avatar-picture {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  overflow: hidden;
  width: 150px;
  height: 150px;
  margin: 0 0 10px 25px;
  z-index: 1;
}

.avatar-picture img {
  width: 120%;
  min-height: 100%;
}

.user-name {
  font-family: "Poppins";
  font-weight: 400;
  font-size: 1.6rem;
  position: absolute;
  z-index: 4;
  color: #558548;
  margin-left: 50px;
  background-color: #ffffff;
  padding: 0px 20px;
  box-shadow: 6px 6px 0 0px #b7da98;
  top: 20px;
}

.avatar-leaves {
  position: absolute;
  z-index: 3;
  top: 25px;
  width: 90px;
}

.bar-toggle {
  width: 10%;
  /* height: 100%; */
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  align-self: flex-end;
  margin-right: 20px;
  margin-top: 25px;
}

.bar-toggle img {
  width: 25px;
  color: white;
  margin-right: 10px;
  cursor: pointer;
  transition: all 0.5s ease-out;
}

.bar-toggle img:hover {
  transform: scale(1.2);
}

.arrow-nav-bar {
  display: flex;
  position: fixed;
  top: 50%;
  transition: all 1s ease-out;
}

.arrow-nav-bar svg {
  /* position: fixed; */
  /* top: 10vh; */
  left: -15px;
  margin: 0;
  width: 50px;
  height: 50px;
  z-index: 5;
  transform: translateX(-50px);
  transition: all 1s ease-out;
}

/* State change */

.hidden-bar {
  transition: all 1s ease-out;
  /* transform: translatex(-100%); */
  margin-left: -470px;
}

.show-arrow {
  transition: all 1s ease-out;
  transform: translateX(500px);
  cursor: pointer;
}

.show-arrow:hover {
  transition: all 0.5s ease-in;
  transform: translateX(510px);
}

.nav-mobile {
  display: none;
}

.toggle-links {
  transform: translateX(0);
}

.active {
  transform: translateX(0);
}

@media only screen and (max-width: 650px) {
  .nav-bar {
    position: fixed;
    top: 0;
    margin: 0;
    display: flex;
    flex-flow: row;
    justify-content: flex-end;
    min-height: 25px;
    height: 35px;
    width: 100vw;
    min-width: 100vw;
    box-shadow: 5px 0 30px 0px rgba(0, 0, 0, 0.178);
    background: #5e8e3f;
    /* border-bottom-right-radius: 35px;
    border-top-right-radius: 35px; */
    z-index: 10;
    transition: all 1s ease-out;
  }

  .nav-links {
    font-family: "Segoe UI";
    color: #545554;
    position: fixed;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
    background-color: white;
    padding: 0 0;
    margin: 0;
    right: 0;
    margin-top: 25px;
    top: 85px;
    height: 50px;
    width: 250px;
    /* transform: translateX(120vw); */
  }
  .nav-link {
    color: #414141;
    text-decoration: none;
    background-color: #dcf7d3;
    font-size: 1.2rem;
    padding: 5px 10px;
    border-bottom: 1px solid #54555471;
    transition: transform 0.2s ease-out;
    height: 50px;
  }

  .arrow-nav-bar svg {
    display: none;
  }

  .bar-toggle {
    display: none;
  }

  .bottom-logo {
    display: none;
  }

  .lower-nav {
    display: none;
  }

  .upper-nav {
    display: none;
  }

  .nav-link:hover {
    color: white;
    background-color: #b7da98;
    border-bottom: 1px solid #ffffff71;
    transform: translateX(0);
    transition: transform 0.2s ease-out;
  }

  .nav-mobile {
    font-size: 1.5rem;
    display: block;
    width: 25px;
    padding-right: 10px;
  }

  .nav-mobile .fas {
    color: white;
    cursor: pointer;
  }

  .active {
    transform: translateX(100%);
  }
}
